.rain-background{position:fixed;top:0;left:0;height:100vh;width:100vw;margin:0;background:linear-gradient(to bottom, #202020, #111119);z-index:0}.rain-background .rain{position:absolute;left:0;width:100%;height:100%;z-index:2}.rain-background .rain .back-row{z-index:1;bottom:60px;opacity:0.5}.rain-background .drop{position:absolute;bottom:100%;width:15px;height:120px;pointer-events:none;-webkit-animation:drop 0.5s linear infinite;animation:drop 0.5s linear infinite}.rain-background .stem{width:1px;height:60%;margin-left:7px;background:linear-gradient(to bottom, rgba(255,255,255,0), rgba(255,255,255,0.25));-webkit-animation:stem 0.5s linear infinite;animation:stem 0.5s linear infinite}.rain-background .splat{width:15px;height:10px;border-top:2px dotted rgba(255,255,255,0.5);border-radius:50%;opacity:1;transform:scale(0);-webkit-animation:splat 0.5s linear infinite;animation:splat 0.5s linear infinite}@-webkit-keyframes drop{0%{transform:translateY(0vh)}75%{transform:translateY(90vh)}100%{transform:translateY(90vh)}}@keyframes drop{0%{transform:translateY(0vh)}75%{transform:translateY(90vh)}100%{transform:translateY(90vh)}}@-webkit-keyframes stem{0%{opacity:1}65%{opacity:1}75%{opacity:0}100%{opacity:0}}@keyframes stem{0%{opacity:1}65%{opacity:1}75%{opacity:0}100%{opacity:0}}@-webkit-keyframes splat{0%{opacity:1;transform:scale(0)}80%{opacity:1;transform:scale(0)}90%{opacity:0.5;transform:scale(1)}100%{opacity:0;transform:scale(1.5)}}@keyframes splat{0%{opacity:1;transform:scale(0)}80%{opacity:1;transform:scale(0)}90%{opacity:0.5;transform:scale(1)}100%{opacity:0;transform:scale(1.5)}}

.campaign-nav-element{cursor:pointer;margin-top:10px;display:flex;justify-content:space-between;align-items:center;padding:5px;border:2px solid #fff;border-radius:10px}.campaign-nav-element h2{margin:0;font-family:"Quicksand",sans-serif;font-weight:100}.campaign-nav-element span{display:flex;align-items:center;height:2em}.campaign-nav-element span svg{width:2em}

#campaign-form{position:fixed;top:0;left:26em;display:flex;flex-direction:column;align-items:center;width:0;overflow-x:hidden;background-color:#040d16}#campaign-form.active{width:22em}#campaign-form .header{display:flex;justify-content:center;align-items:center}#campaign-form .header h1{font-family:"Quicksand",sans-serif;font-weight:100}#campaign-form .header button{border:0;background-color:transparent}#campaign-form .body{display:flex;flex-direction:column;align-items:center}#campaign-form .body #img-label{margin:10px 0 0 0;height:6em;width:92%;display:flex;border:2px dashed #fff;border-radius:10px;padding:5px;cursor:pointer}#campaign-form .body #img-label input{width:0.1px;height:0.1px;opacity:0;overflow:hidden;position:absolute;z-index:0}#campaign-form .body input{width:92%;border-radius:10px;border:2px solid #fff;background-color:transparent;font-size:1.5em;color:#fff;font-family:"Quicksand",sans-serif;font-weight:100;padding:5px}#campaign-form .body textarea{margin:10px 0 0 0;width:92%;border-radius:10px;border:2px solid #fff;background-color:transparent;font-size:1.5em;color:#fff;font-family:"Quicksand",sans-serif;font-weight:100;padding:5px}#campaign-form .body .options{display:flex;width:100%;margin:0 0 10px 0;justify-content:space-between}#campaign-form .body .options button{align-self:start;border:2px solid #fff;background-color:transparent;color:#fff;font-family:"Quicksand",sans-serif;font-size:1.5em;font-weight:100;border-radius:10px;margin:10px 2% 0 2%;padding:5px}#campaign-form .body .options #campaigns-nav-advanced-options{align-self:end;font-size:1em;border:0}

#default-campaign-sidebar .main-sidebar{float:left;display:flex;flex-direction:column;justify-content:left;align-items:center;width:22em;z-index:10}#default-campaign-sidebar .main-sidebar .title{display:flex;justify-content:center}#default-campaign-sidebar .main-sidebar .title h1{font-family:"Quicksand",sans-serif;font-weight:100}#default-campaign-sidebar .main-sidebar .search{display:flex;justify-content:center}#default-campaign-sidebar .main-sidebar .search input{background:transparent;color:#fff;border-top:2px solid #fff;border-left:2px solid #fff;border-bottom:2px solid #fff;border-right:0;border-top-left-radius:10px;border-bottom-left-radius:10px;font-size:1.5em}#default-campaign-sidebar .main-sidebar .search button{display:flex;justify-content:center;align-items:center;background-color:transparent;border:2px solid #fff;border-radius:10px}#default-campaign-sidebar .main-sidebar .search button svg{height:3em}#default-campaign-sidebar .main-sidebar .campaigns{width:92%}#default-campaign-sidebar .main-sidebar .campaigns .new-campaign-nav-element.active{color:#040d16;background-color:#fff}

#default-sidebar{position:relative;overflow-x:hidden;background-color:#040d16;color:#fff;height:100vh;width:4em;transition:ease-in-out 0.75s;z-index:5}#default-sidebar.active{width:26em;transition:overflow-x none 0s ease 0s ease-in-out 0.75s;transition:overflow-x initial ease-in-out 0.75s}#default-sidebar.active #background{width:26em !important}#default-sidebar #background{position:absolute;top:0;left:0;height:100%;max-height:100vh;width:26em;overflow:hidden;transition:ease-in-out 0.75s;z-index:7}#default-sidebar .sidebar-flex{display:flex;max-height:100vh}#default-sidebar .sidebar-flex .nav{float:left;width:4em;height:100vh;display:flex}#default-sidebar .sidebar-flex .nav .nav-links{display:flex;flex-direction:column;justify-content:space-between;align-content:center;width:100%}#default-sidebar .sidebar-flex .nav .nav-links button{position:relative;background-color:transparent;border:0;z-index:10}#default-sidebar .sidebar-flex .nav .nav-links .top{display:flex;flex-direction:column;justify-content:left;align-items:center}#default-sidebar .sidebar-flex .nav .nav-links .top .logo{fill:#fff}#default-sidebar .sidebar-flex .nav .nav-links .top svg{position:relative;margin-top:10px;width:2.5em;padding:5px;cursor:pointer;z-index:10}#default-sidebar .sidebar-flex .nav .nav-links .bottom{display:flex;flex-direction:column;justify-content:left;align-items:center}#default-sidebar .sidebar-flex .nav .nav-links .bottom svg{margin-top:20px;padding:5px;cursor:pointer}

